import React, { useEffect, useState } from 'react';
import './contect.css';
import hour from '../image/hour 24.png';
import communication from '../image/communication.png';
import message from '../image/message.png';
import location from '../image/location.png';

export default function Contact({ setShowModel }) {
  const [address, setAddress] = useState("A108 Adam Street Newyork, NY536487");
  const [phoneNumbers, setPhoneNumbers] = useState(['+5674 89788', '+5674 89788']);
  const [emails, setEmails] = useState(['info@example.com', 'contact@example.com']);
  const [openHours, setOpenHours] = useState(['Monday-friday 9:00AM-5:00pm']);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });
  const [formSubmitted, setFormSubmitted] = useState(false);

  useEffect(() => {
    if (formSubmitted) {
      // Execute after form submission
      setTimeout(() => {
        setShowModel(true);
      }, 500); // Change the timeout to 500 milliseconds (0.5 seconds)
    } 
  }, [formSubmitted, setShowModel]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle form submission
    console.log(formData);
    // Clear form after submission
    setFormData({
      name: '',
      email: '',
      subject: '',
      message: ''
    });
    // Trigger useEffect by setting formSubmitted to true
    setFormSubmitted(true);
  };

  return (
    <>

  
    <div className='body-content'>
        <div className="content-mainbox">
          <div className="content-firstbox">
            <div className="content-firstflex">
              <div className="content-address">
                <img src={location} alt="" />
                <h3>Address</h3>
                <p>{address}</p>
              </div>
              <div className="content-callus">
                <img src={communication} alt="" />
                <h3>Call Us</h3>
                {phoneNumbers.map((phoneNumber, index) => (
                  <p key={index}>{phoneNumber}</p>
                ))}
              </div>
            </div>
            <div className="content-secondflex">
              <div className="content-emailus">
                <img src={message} alt="" />
                <h3>Email Us</h3>
                {emails.map((email, index) => (
                  <p key={index}>{email}</p>
                ))}
              </div>
              <div className="content-openhours">
                <img src={hour} alt="" />
                <h3>Open Hours</h3>
                {openHours.map((hour, index) => (
                  <p key={index}>{hour}</p>
                ))}
              </div>
            </div>
          </div>
          
          <div className="content-form ">
            
        

          <form onSubmit={handleSubmit}>
              <label className="content-yourname" htmlFor="name">Name</label>
              <input className="content-yoursemail" id="name" name="name" value={formData.name} onChange={handleChange} required />
              <label className="content-youremail" htmlFor="email"> Email </label>
              <input className="content-yoursemail" id="email" name="email" value={formData.email} onChange={handleChange} required /><br />
              <label htmlFor="subject">Subject: </label>
              <input className="subject" name="subject" value={formData.subject} onChange={handleChange} required /><br />
              <label htmlFor="message">Message:</label>
              <textarea className="content-message" name="message" value={formData.message} onChange={handleChange} required /><br />
              <div className='button-div'><button className="content-button" type="submit">Submit</button> </div>
            </form>
          </div>
           
            </div>
          
          </div>
      
    
     
    </>
  );
}
