import React, { useState } from 'react';
import './selling.css';
import RangeSlider from 'react-slider';


const Selling = () => {
    const [formData, setFormData] = useState({
        Name: '',
        projectname: '',
        mobileNumber: '',
        gmail: '',
        price: '',
        message: ''
    });

    const [zipFiles, setZipFiles] = useState([]);
    const [imageFiles, setImageFiles] = useState([]);
    const [min, setMin] = useState(0);
    const [max, setMax] = useState(100000); // Keep the max value same
    const [priceRange, setPriceRange] = useState([0, 100000]); // Initial state
    const [tempMin, setTempMin] = useState(0);
    const [tempMax, setTempMax] = useState(100000); // Initial state

    const handleRangeChange = (value) => {
        setPriceRange(value);
        setMin(value[0]);
        setMax(value[1]);
        setTempMin(value[0]);
        setTempMax(value[1]);
    };

    const updateMin = (e) => {
        const newMin = parseInt(e.target.value, 10);
        setTempMin(newMin);
        if (!isNaN(newMin) && newMin >= 0 && newMin <= max) {
            setMin(newMin);
            setPriceRange([newMin, max]);
        }
    };

    const updateMax = (e) => {
        const newMax = parseInt(e.target.value, 10);
        setTempMax(newMax);
        if (!isNaN(newMax) && newMax >= min && newMax >= 0) {
            setMax(newMax);
            setPriceRange([min, newMax]);
        }
    };

    const handleBlurMin = () => {
        if (isNaN(tempMin) || tempMin < 0 || tempMin > max) {
            setTempMin(min); // Reset to last valid min value
        }
    };

    const handleBlurMax = () => {
        if (isNaN(tempMax) || tempMax < min || tempMax < 0) {
            setTempMax(max); // Reset to last valid max value
        }
    };
  

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleZipChange = (e) => {
        const files = Array.from(e.target.files);
        const validFiles = files.filter(file => file.type === 'application/zip');
        if (validFiles.length !== files.length) {
            alert('Some files are not ZIP files and will not be uploaded.');
        }
        setZipFiles([...zipFiles, ...validFiles]);
    };

    const handleImageChange = (e) => {
        const files = Array.from(e.target.files);
        const validFiles = files.filter(file => {
            if (file.type.startsWith('image/') && file.size <= 10 * 1024 * 1024) {
                return true;
            } else {
                if (file.size > 10 * 1024 * 1024) {
                    alert(`${file.name} exceeds the 10MB size limit and will not be uploaded.`);
                } else {
                    alert(`${file.name} is not a valid image file and will not be uploaded.`);
                }
                return false;
            }
        });
        setImageFiles([...imageFiles, ...validFiles]);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Handle form submission logic, such as uploading files to a server
        console.log('Form Data:', formData);
        console.log('ZIP Files:', zipFiles);
        console.log('Image Files:', imageFiles);
        console.log('Price Range:', priceRange);
    };

    const handleDeleteZip = (index) => {
        const updatedFiles = [...zipFiles];
        updatedFiles.splice(index, 1);
        setZipFiles(updatedFiles);
    };

    const handleDeleteImage = (index) => {
        const updatedFiles = [...imageFiles];
        updatedFiles.splice(index, 1);
        setImageFiles(updatedFiles);
    };

  




    return (
        <div className="allsell">
            <div className="head-sell text-nowrap">PROJECT SELLING FORM</div>
            <div className="container contsell">
                <div className="body-sell">
                    <form onSubmit={handleSubmit}>
                        <div className="row sell-project">
                            <div className="form-group col-lg-3 col-1 sell">
                                <input
                                    className='input-sell'
                                    type="text"
                                    id="Name"
                                    name="Name"
                                    value={formData.Name}
                                    onChange={handleChange}
                                    required
                                />
                                <label className='label-sell' htmlFor="Name">Name</label>
                            </div>
                            <div className="form-group col-lg-3 col-1 sell">
                                <input
                                    className='input-sell'
                                    type="text"
                                    id="projectname"
                                    name="projectname"
                                    value={formData.projectname}
                                    onChange={handleChange}
                                    required
                                   
                                />
                                <label className='label-sell' htmlFor="projectname">Project Name</label>
                            </div>
                            <div className="form-group col-lg-3 col-1 sell">
                                <input
                                    className='input-sell'
                                    type="text"
                                    id="mobileNumber"
                                    name="mobileNumber"
                                    value={formData.mobileNumber}
                                    onChange={handleChange}
                                    required
                                 
                                />
                                <label className='label-sell' htmlFor="mobileNumber">Mobile Number</label>
                            </div>
                            <div className="form-group col-lg-3 col-1 sell">
                                <input
                                    className='input-sell'
                                    type="email"
                                    id="gmail"
                                    name="gmail"
                                    value={formData.gmail}
                                    onChange={handleChange}
                                    required
                                   
                                />
                                <label className='label-sell' htmlFor="gmail">Email</label>
                            </div>
                            <div className="form-group col-lg-12 col-12 sell">
                           
                            <RangeSlider
                min={0}
                max={100000} // Keep the max value same
                value={priceRange}
                onChange={handleRangeChange}
                className="slider"
                pearling
                step={100}
            />
                <div className="price-range-labels">
                                
                <h6>Min-Price:</h6>
                <input
                    className="input-h6"
                    type="number"
                    value={tempMin}
                    onChange={updateMin}
                    onBlur={handleBlurMin}
                    min={0}
                    step={1}
                />
                <h7>Max-Price:</h7>
                <input
                    className="input-h7"
                    type="number"
                    value={tempMax}
                    onChange={updateMax}
                    onBlur={handleBlurMax}
                    min={0}
                    step={1}
                />

                                 
                               
                                
                                 
                                      
                               
                                    
                                </div>
                            </div>
                            <div className="form-group col-lg-12 col-12 sell-zip">
                                <label className='label-file1' htmlFor="zipFileInput">Upload only ZIP files</label>
                                <input
                                    type="file"
                                    accept=".zip"
                                    id="zipFileInput"
                                    multiple
                                    onChange={handleZipChange}
                                    placeholder='Upload ZIP files'
                                />
                                {zipFiles.map((file, index) => (
                                    <div className="file-item" key={index}>
                                        <span >{file.name}
                                        <button type="button" onClick={() => handleDeleteZip(index)}>x</button>
                                        </span>
                                       
                                    </div>
                                ))}
                            </div>
                            <div className="form-group col-lg-12 col-12 sell">
                              <label  className='label-file' htmlFor="imageFileInput">Upload Image files</label>
                                <input
                                    type="file"
                                    accept="image/*"
                                    id="imageFileInput"
                                    multiple
                                    onChange={handleImageChange}
                                />
                                {imageFiles.map((file, index) => (
                                    <div className="file-item" key={index}>
                                        <span>{file.name}
                                        <button type="button" onClick={() => handleDeleteImage(index)}>x</button>
                                        </span>
                                        
                                    </div>
                                ))}
                            </div>
                            <div className="form-group col-lg-12 col-12 sell">
                                <textarea
                                    id="message"
                                    name="message"
                                    value={formData.message}
                                    onChange={handleChange}
                                    rows={4}
                                    required
                                />
                                <label  className='sell-label' htmlFor="message">Enter your message...</label>
                            </div>
                        </div>
                        <button className='sell-button' type="submit">Submit</button>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default Selling;
